<template>
    <div id="bgimg">
      <body id="poster">
        <el-form class="login-container" label-position="left" label-width="0px">
          <h3 class="login_title">Login</h3>
          <el-form-item label="">
            <el-input
              type="text"
              v-model="loginForm.loginName"
              autocomplete="off"
              placeholder="username"
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input
              type="password"
              v-model="loginForm.password"
              autocomplete="off"
              placeholder="Password"
              prefix-icon="el-icon-lock"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-select v-model="loginForm.identity" placeholder="Please select identity"prefix-icon="el-icon-user">
              <el-option label="Ordinary user" value="Ordinary user"></el-option>
              <el-option label="Administrator" value="Administrator" ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="width: 100%;">
            <el-button
              type="primary"
              style="width: 100%; background: #505458; border: none"
              v-on:click="Login"
              >Login</el-button
            >
          </el-form-item>
          <el-button @click="toRegister">Click me to register</el-button>
        </el-form>
      </body>
    </div>
  </template>
  
  <script>
  export default {
    name: "Login",
    data() {
      return {
        loginForm: {
          loginName: "",
          password: "",
          identity:""
        }
      };
    },
    methods: {
      Login() {
        // 假设登录接口返回一个 token
        const mockToken = 'mockUserToken'; // 模拟 token
        localStorage.setItem('token', mockToken);
        // 登录成功后，根据 query.redirect 跳转到目标页面，默认跳转到 Home
        this.$router.push(this.$route.query.redirect || '/Home');
        //console.log('submit!',this.loginForm);
        const loginName = this.loginForm.loginName; // 保存用户名
        this.axios.post("http://localhost:3231/sys-user/login", this.loginForm).then((resp) => {
          let data = resp.data;
          if (data.success) {
            this.loginForm = {};
            this.$message({
              message: `Welcome to the main page, ${loginName}!`,
              type: "success"
            });
            if (loginName === "Admin") {
        this.$router.push({ path: "/Admin" }); // 跳转到管理员页面
      } else {
        this.$router.push({ path: "/Home" }); // 跳转到普通用户页面
      }
          } else {
            if (data.message === "User not found") {
              this.$message({
                message: "Username does not exist, please register.",
                type: "error"
              });
            } else if (data.message === "Incorrect password") {
              this.$message({
                message: "Incorrect password, please try again.",
                type: "error"
              });
            } else {
              this.$message({
                message: "Login failed, please try again.",
                type: "error"
              });
            }
          }
        });
      },
      toRegister() {
        this.$router.push({ path: "/Register" });
      }
    }
  };
  </script>
  
  <style>
  #poster {
    background-position: center;
    height: 100%;
    width: 100%;
    background-size: cover;
    position: fixed;
  }
  body {
    margin: 0px;
    padding: 0px;
  }
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  </style>
  